*{box-sizing:border-box;margin:0;padding:0;font-size:18px;line-height:1.2}body{font-family:"Roboto", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;background:#201535 url("https://geekychristine.github.io/a11y-fail/img/memphis-pattern.jpg") repeat;color:#5d576b}button{border:none;color:#fcfcfc;background:#5d576b}[data-modal-intro]{display:flex;justify-content:center;margin:0 auto}.instructions{display:block;background:#fcfcfc !important;width:80%;margin:20px auto !important}.instructions .heading{margin-top:20px}.instructions p{margin:10px 0}marquee{display:block;background:white;padding:10px}.heading{font-family:"Lemonada", cursive;color:#5d576b;margin:0;margin-bottom:12px}.heading--large{font-size:72px}.heading--medium{font-size:48px}.heading--small{font-size:32px}.heading--xsmall{font-size:10px}.text-body{color:#5d576b;font-size:16px}.text-body--large{font-size:24px}.text-body--small{font-size:12px}.text-body--xsmall{font-size:10px}.btn{padding:8px;border-radius:4px;line-height:1}.btn--link{background:none;color:#5d576b}.card{padding:40px;border-radius:4px;box-shadow:0 0 5px rgba(0,0,0,0.1);display:block;margin:40px;background:rgba(255,255,255,0.8)}.cart-container{display:flex;flex-direction:column;justify-content:space-between;padding:40px;margin:40px;width:50%;min-width:300px}.cart-list{margin:auto 0;list-style:none}.cart--product{display:flex;justify-content:space-between;padding:2px 0}.cart--product-price{margin-left:auto}.cart-total{display:flex;justify-content:space-between;border-top:2px dotted #5d576b;margin-top:20px;padding-top:20px}.modal{display:none;position:relative;width:auto;height:auto;z-index:1000;font-size:12px}.modal-body{opacity:0;visibility:hidden;position:absolute;overflow-y:auto;overflow-x:hidden;color:#5d576b;background-color:#99e1d9;width:100vw;height:100vh;border-radius:4px;padding:140px}.show-modal{display:block;position:fixed;top:0;width:100%;height:100%}.show-modal .modal-body{opacity:1;visibility:visible;transform:scale(1);transition:visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s}.show-modal .floater-btn-close{display:inline-block;font-size:10px}body.shift .modal,body.shift .modal-body{z-index:90}.modal-header{margin-bottom:40px}.modal-footer{margin-top:40px;text-align:center}.product-inventory{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:10px}.product-item{display:flex;flex-direction:column;justify-content:flex-end;min-width:100%;min-height:200px;border-radius:20px;overflow:hidden;background-size:cover}.product-info{display:flex;flex-direction:column;text-align:center;width:100%;height:100%;padding:20px;color:#fcfcfc;background-color:rgba(0,0,0,0.1)}.product-name{display:block;width:100%;font-size:12px;line-height:2}.product-cost{display:block;width:100%;font-size:18px;font-weight:bold;color:#99e1d9}.product-button{background:#fffae3;background:linear-gradient(90deg, #fffae3 0%, #ffcad4 50%, #fcfcfc 100%);margin-top:20px;font-size:20px;font-weight:900;color:#5d576b}.header{display:block;padding:30px 100px;margin-bottom:50px;background:#000;text-align:center;color:#fcfcfc}.layout{display:flex;flex:1 0 auto;justify-content:flex-start;align-items:flex-start}
